import {formatTimeToStr, formatNera} from "../../plugins/utils";
import {mapState} from 'vuex'

export default {
  name: "Publish",
  data() {
    return {
      // step: 0,
      minDate: new Date(1888, 0, 1),
      minCurrentDate: new Date(new Date().getFullYear(), new Date().getMonth(), new Date().getDate()),
      maxDate: new Date(2099, 12, 31),
      sDateShow: false,
      iRewardShow: false,
      iExpirationDateShow: false,
      sLocationShow: false,
      iAmountCount: 0,
      NeraList: [50, 100, 200, 500, 1000],
      dateList: [],
      columns: [
        {text: this.$t('Day01'), value: 3, type: 1},
        {text: this.$t('Day02'), value: 5, type: 1},
        {text: this.$t('Day03'), value: 7, type: 1},
        {text: this.$t('Day04'), value: 1, type: 2},
        {text: this.$t('Day05'), value: 2, type: 2},
        {text: this.$t('Day06'), value: 3, type: 2},
        {text: this.$t('Day07'), value: 4, type: 2},
        {text: this.$t('Day08'), value: 5, type: 2},
        {text: this.$t('Day09'), value: 6, type: 2},
        {text: this.$t('Day10'), value: 7, type: 2},
        {text: this.$t('Day11'), value: 8, type: 2},
        {text: this.$t('Day12'), value: 9, type: 2},
        {text: this.$t('Day13'), value: 10, type: 2},
        {text: this.$t('Day14'), value: 11, type: 2},
        {text: this.$t('Day15'), value: 1, type: 3},
      ],
      oReward: {
        sDate2: new Date(),
        sSubject: 'I want to find some pictures of '
        // "sWallet": "0x1B1432B715638d520bd6C20e4fb5C8De65D8ae47",
        // "sSubject": new Date().getTime() + "年的星空照片,要4k以上",
        // "sDate": "1205/04/01",
        // "sLocation": "beijing",
        // "iReward": "40.36",
        // "iExpirationDate": 1625021721000,
        // "sDescription": "压死猫啦"
      }
    }
  },
  computed: {
    ...mapState({
      step: (state) => state.reward.step,
      nera: (state) => state.nera,
    }),
    publishStep1() {
      return this.iAmountCount < 0 ||
        !this.oReward.sSubject ||
        !this.oReward.iReward
    }
  },
  watch: {
    step(v) {
      return v
    },
    nera(v) {
      return v
    }
  },
  mounted() {
    this.getAmountCount(0)
  },
  methods: {
    asyncValidator(val, rule) {
      return new Promise(async (resolve) => {
        // console.log(val, rule);
        if (val < 1 || val > 10000) {
          rule['message'] = this.$t('Main12')
          resolve(false)
        }
        this.$toast.loading(this.$t('Main31'));
        let iAmountCount = await this.getAmountCount(val)
        this.$toast.clear();
        if (iAmountCount < 0) {
          rule['message'] = this.$t('Main32')
          resolve(false)
        }
        resolve(true)
      });
    },
    async handleSubmit() {
      if (this.step === 0) {
        this.$store.commit('reward/SET_STEP', 1)
        return
      }
      let data = {
        ...this.oReward,
        iExpirationDate: new Date(this.oReward.iExpirationDate.replace(/-/g, '/') + " 23:59:59").getTime()
      }
      delete data.sDate2
      let res = await this.$axios.post('/v1/emall/CreateRewardsInfo', data)

      this.$router.replace({
        name: 'Main',
        params: {
          iRewardId: res.iRewardId
        }
      })
      this.$toast(this.$t('Tip01'))
      this.$store.dispatch('UPDATE_USERINFO')

      this.$store.commit('reward/SET_STEP', 0)
      console.log(res);
    },
    async getAmountCount(val) {
      let {vBalance} = await this.$axios.post('/v1/udc/GetUserBalance', {
        iAcctType: 0
      })
      // console.log(vBalance);
      if (vBalance.length) {
        vBalance = vBalance[0]
        let iAmount = formatNera(vBalance.iAvailableAmount)
        // console.log(iAmount - val);

        this.iAmountCount = formatNera(iAmount - val)
        return this.iAmountCount
      }
    },
    onFinish() {

    },
    handleCancel() {
      this.sDateShow = false
      this.iExpirationDateShow = false
      this.sLocationShow = false
    },
    handleDateConfirm(value) {
      let date = formatTimeToStr(value, 'yyyy-MM-dd')
      this.$set(this.oReward, 'sDate', date)
      this.$set(this.oReward, 'sDate2', date)
      // console.log(date);
      this.handleCancel()
    },
    handleDate2Confirm(value) {
      //  type 1 - 日,  2 - 月, 3 - 年
      let today = new Date()
      switch (value.type) {
        case 1:
          today.setDate(today.getDate() + value.value)
          break
        case 2:
          today.setMonth(today.getMonth() + value.value)
          break
        case 3:
          today.setFullYear(today.getFullYear() + value.value)
          break
      }
      let date = formatTimeToStr(today, 'yyyy-MM-dd')

      this.$set(this.oReward, 'iExpirationDate', date)
      this.$set(this.oReward, 'iExpirationDate2', date)
      //


      this.handleCancel()
    },
    handleLocationConfirm(value, index) {
      // console.log(value.join());
      this.$set(this.oReward, 'sLocation', value.join('/'))
      this.handleCancel()
    },
    onClose(vv) {
      if (!this.oReward.iReward) {
        this.getAmountCount(0)
      }
      this.$refs.oForm.validate('iReward')
    },
    onInput(v) {
      let value = this.oReward.iReward || ''
      value = value + '';
      if (value === '' && v === '.') {
        value = '0';
      } else if (value.indexOf('.') !== -1 && v === '.') {
        return;
      } else if (value.indexOf('0') !== -1 && value.length === 1 && v === 0) {
        return;
      } else if (/\.\d{2}$/.test(value)) {
        return;
      }
      value += v;
      this.$set(this.oReward, 'iReward', value)
    },
    onDelete() {
      let value = this.oReward.iReward || ''
      if (!value) {
        return;
      }
      value = value.substring(0, value.length - 1);
      this.$set(this.oReward, 'iReward', value)
    }
  },
}
